@import "../../helpers/styles.scss";

.auth-layout {
  background: url("https://wallpapercave.com/wp/wp11926801.jpg");
  background-color: #d6f2fe7c;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: lighten;
  box-shadow: 1px 15px 40px #000000;
  background-size: cover;
  height: 100dvh;
  width: 100dvw;
  overflow: hidden;
  display: grid;
  place-items: center;
  .auth-card {
    height: 80vh;
    width: 70vw;
    background: white;
    border-radius: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    overflow: hidden;
    
    img{
      width: 90%;
      height: 100%;
    }
   
  }
  .fieldWidth{
    width: 99%;
   }
   .signupScroll{
    overflow: scroll;
    scrollbar-width: none;
    // overflow-x: hidden;
   }
  @media only screen and (max-width: 768px) {
    .auth-card {
     height: 90vh;
     display: flex;
     flex-direction: column;
     overflow: scroll;
     scroll-behavior: smooth;
     scrollbar-width: none;
    //  overflow-x: hidden;
    
    }
   .fieldWidth{
    width: 110%;
   }
   .signupScroll{
    overflow: auto;
    // overflow-x: hidden;
   }
}
}